import { AccessToken } from './access-token.model'

/**
 * Access Token Mock
 */
export function AccessTokenMock(options?: Partial<AccessToken>): AccessToken {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'ACCESS_TOKEN_ID',

    signature: options?.signature || 'ACCESS_TOKEN_SIGNATURE',
    permission: options?.permission || 'READ',

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
  }
}
